// src/app/produto/[id]/page.js
'use client';

import { useParams } from 'next/navigation';
import products from '../../../data/products.json';
import { useCart } from '../../../context/CartContext'; // ← Certifique-se de que seja o caminho correto

export default function ProdutoPage() {
  const params = useParams();
  const id = parseInt(params.id);
  const { addItemToCart } = useCart(); // Função do contexto

  const product = products.find((p) => p.id === id);

  if (!product) {
    return <div>Produto não encontrado.</div>;
  }

  return (
    <main className="min-h-screen p-6 bg-gray-100">
      <div className="max-w-3xl mx-auto bg-white rounded-lg shadow-md p-6">
        <h1 className="text-2xl font-bold mb-4">{product.name}</h1>
        <div className="w-full h-64 mb-4 flex items-center justify-center overflow-hidden rounded">
          <img
            src={product.image || '/images/default-product.jpg'}
            alt={product.name}
            className="object-cover w-full h-full"
          />
        </div>
        <p className="text-gray-700 mb-4">{product.description}</p>
        <p className="text-green-600 text-xl font-bold mb-4">R$ {product.price.toFixed(2)}</p>

        {/* Botão para adicionar ao carrinho */}
        <button
          onClick={() => addItemToCart(product)}
          className="bg-red-600 hover:bg-red-700 text-white py-2 px-4 rounded transition duration-200"
        >
          Adicionar ao Carrinho
        </button>
      </div>
    </main>
  );
}
